@mixin trs {
	transition: all 0.2s; }


/* ===================================
	 Main Styles
	 @mixin ================================== */
body {
	position: relative;
	min-width: 320px;
	font-family: $default-fonts;
	font-size: 16px;
	color: #333;
	line-height: 1.42857; }

.wrapper {
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	min-height: 100%;
	padding-bottom: 40px; }

.container {
 }	//+container


/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.vertical {
	display: inline-block;
	vertical-align: middle; }

.cfx {
	@include pie-clearfix; }

