// px to em
$browser-context: 16;
@function em($pixels, $context: $browser-context) {
	@return #{$pixels/$context}em; }

// clearfix
@mixin pie-clearfix {
	&:after {
		content: "";
		display: table;
		clear: both; } }

// circle
@mixin circle-fix($circle-width, $circle-color) {
	border-radius: 50%;
	border: $circle-width+px solid $circle-color;
	box-shadow: 0 0 1px $circle-color; }

@mixin circle-in-fix($circle-width, $circle-color) {
	border-radius: 50%;
	border: $circle-width+px solid $circle-color;
	box-shadow: 0 0 1px $circle-color, inset 0 0 1px $circle-color; }

// width
@mixin r($width) {
	@media only screen and (max-width: $width+ "px") {
		@content; } }

// font-face
@mixin font($alias, $name) {
	@font-face {
		font-family: $alias;
		src: url("../css/fonts/" + $name + ".eot");
		src: url("../css/fonts/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../css/fonts/" + $name + ".woff") format("woff"), url("../css/fonts/" + $name + ".woff2") format("woff2"), url("../css/fonts/" + $name + ".ttf") format("truetype");
		font-weight: normal;
		font-style: normal; } }
// cover
@mixin cover {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%; }

// placeholder
@mixin placeholder-color($color) {
	&:-moz-placeholder,
	&::-moz-placeholder {
		color: $color;
		opacity: 1; }
	&::-webkit-input-placeholder {
		color: $color; } }

// unselectable
@mixin unselectable {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none; }

@mixin nosel {
	@include unselectable; }

// selection
@mixin selection {
	& ::selection {
		@content; }
	& ::-moz-selection {
		@content; } }

//triangle
@mixin arr($width, $height, $bg, $direction) {
	width: 0px;
	height: 0px;
	border-style: solid;
	@if $direction == t {
		border-width: 0 $width / 2 + px $height + px $width / 2 + px;
		border-color: transparent transparent $bg transparent; }
	@if $direction == r {
		border-width: $height / 2 + px 0 $height / 2 + px $width + px;
		border-color: transparent transparent transparent $bg; }
	@if $direction == b {
		border-width: $height + px $width / 2 + px 0 $width / 2 + px;
		border-color: $bg transparent transparent transparent; }
	@if $direction == l {
		border-width: $height / 2 + px $width + px $height / 2 + px 0;
		border-color: transparent $bg transparent transparent; }
	@if $direction == tl {
		border-width: $height + px $width + px 0 0;
		border-color: $bg transparent transparent transparent; }
	@if $direction == tr {
		border-width: 0 $width + px $height + px 0;
		border-color: transparent $bg transparent transparent; }
	@if $direction == br {
		border-width: 0 0 $height + px $width + px;
		border-color: transparent transparent $bg transparent; }
	@if $direction == bl {
		border-width: $width + px 0 0 $height + px;
		border-color: transparent transparent transparent $bg; } }
// example
// +arr(500,50, #000, l)
// +arr(500,50, #000, bl)

@mixin vcenter {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%); }

@mixin vertical {
	font-size: 0;
	text-align: center;
	&:before {
		content: "";
		display: inline-block;
		vertical-align: middle;
		width: 0;
		height: 100%; } }

@mixin hide-text {
	font: 0/0 a;
	text-shadow: none;
	color: transparent; }
@mixin hidetext {
	@include hide-text; }

@mixin text-overflow {
	overflow: hidden;
	-ms-text-overflow: ellipsis;
	text-overflow: ellipsis;
	white-space: nowrap; }

@mixin ratio($width, $height) {
	position: relative;
	&:before {
		display: block;
		content: "";
		width: 100%;
		padding-top: ($height / $width) * 100%; } }

@mixin ratio-in {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0; }

// justify, set on parent
@mixin justify {
	text-align: justify;
	line-height: 0;
	font-size: 0;
	text-justify: newspaper;
	zoom: 1;
	text-align-last: justify;
	&:after {
		content: "";
		display: inline-block;
		width: 100%;
		height: 0;
		visibility: hidden;
		overflow: hidden; } }
